body {
  margin: 50px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #101516;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Container{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.884);
    box-shadow: 3px 0px 40px -13px #ffff;
    width: 23rem;
    margin: auto;
    justify-content: center;
    align-items: center;
    border-radius: 37px;
}
.lastPart{
    color: #ff0000;
    font-size: 18px;
    margin: 20px 0px;
    font-family: fantasy;
    box-sizing: border-box;
    /* background: #000000; */
    border: 0px;
    box-shadow: 1px 17px 15px -10px #ff5c5c;
}
h2{
    display: flex;
    justify-content: center;
    align-items: center;
    color: aliceblue;
}
*{
    color: white;
}
.card{
    display: flex;
    margin: 13px 10px;
}
.text{
    /* display: flex; */
    margin: 0px 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.text span:nth-child(1){
    margin-bottom: 20px;
    display: flex;
    color: white;
    font-weight: 900;
    font-size: 23px;
}
.text span:nth-child(2){
    margin: 45px;

}
.tM span:nth-child(2){
    color: green;
}
.rM span:nth-child(2){
    color: rgb(255, 0, 0);
}
hr{
    size: 10rem;
}
.taskBag{
    background-color: #151d22;
    width: 20rem;
    border-radius: 6px;
    font-size: 17px;
    font-weight: 500;
    margin-bottom: 11px;
    padding: 7px 10px;
    position: relative;
    text-align: left;
}
.taskNumber{
    color: #383838;
    border: 1px solid;
    border-radius: 50%;
    padding: 0px 1px 0px 9px;
    margin: 0px 10px 0px 3px;
}
.delIcon{
    float: right;
}
.delIcon button{
    background-color: #151d22;
    border: 0px;
}
.delIcon button:hover{
cursor: pointer;
float: right;
background-color: #355061;
padding-top: 4px;
padding-left: 7px;
margin-top: -3px;
border-radius: 50%;
}
.hr2{
    height: 4px;
    background-color: white;
    border-radius: 3px;
}
form input, button{
    background-color: #ffffff;
    /* border: 1px solid white; */
    box-sizing: border-box;
    box-shadow: 0 0px 61px -10px #ff0e0e;
}
form .textWala{
    color: #000000;
    border-radius: 7px;
    width: 200px;
    font-size: 15px;
    font-weight: 800;
    padding: 2px 0px 4px 10px;
    border: 0px;
}
form .NumWala{
    color: #000000;
    margin-left: 10px;
    width: 85px;
    border-radius: 7px;
    font-size: 15px;
    font-weight: 800;
    padding: 2px 0px 4px 10px;
    border: 0px;
}
form button{
    float: right;
    margin-top: 1px;
    margin-right: 9px;
    background-color: #ffffff;
    border-radius: 50%;
    width: 28px;
    padding: 0px;
    height: 27px;
}
form button:hover{
cursor: pointer;
float: right;

}
form path{
    color: green;
}
.gain{
    float: left;
}
.loss{
    float: left;
}
.gain path{
    color: red;
}
.loss path{
    color: green;
}
